import {OK, NO_CONTENT} from 'http-status-codes';
import {API_URL, HEADERS, CONFIG} from "MCT/utils/api";
import axios from 'axios';

import {addingRequest, deletedRequest, addRequest, addRequestError} from 'MCT/store/actions/index';

const postRequest = (payload) => {

    return function (dispatch) {

        dispatch(addingRequest);

        return axios.post(`${API_URL}/request`, payload)
            .then(response => {
                switch (response.status) {

                    case OK:
                        return response.data;

                    case NO_CONTENT:
                        return {};
                }
            })
            .then(request => {
                dispatch(addRequest(request));
            })
            .catch(error => {
                dispatch(addRequestError(error));
            })
    };
};

const deleteRequest = (requestId) => {

    return function (dispatch) {

        return axios.patch(`${API_URL}/request/${requestId}/markas/archived`)
            .then(response => {
                switch (response.status) {
                    case OK:

                        console.log("RESPONSE", response);

                        dispatch(deletedRequest(requestId));
                        return response;
                }
            })
    }
};

export {
    postRequest,
    deleteRequest
};