import React from "react";
import ReactTable from 'react-table';
import PropTypes from 'prop-types';
import {ReactTableDefaults} from "react-table";
import {connect} from 'react-redux';

import {deleteRequest} from "MCT/store/action-creators/requests";
import StoreButton from "MCT/components/core/store-button";


Object.assign(ReactTableDefaults, {
    showPagination: true,
    filterable: false,
    sortable: true,
    resizable: false,
    minRows: 15,
    defaultPageSize: 15,
});

const mapDispatchToProps = {
    deleteRequest
};

const columnTypes = {
    storeAction: (row, col, actionFunction) => {

        if (!col.action || col.action === "") {
            return;
        }

        return <StoreButton onClick={() => actionFunction(row.original.id)} buttontext={col.text}/>
    }
};


class RequestList extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <ReactTable
                columns={this.props.columns}
                data={this.props.data}
                getTrProps={(state, rowInfo, column, instance) => {
                    return {
                      onClick: (e, handleOriginal) => {
                        //console.log("Click through!", e);


                        // IMPORTANT! React-Table uses onClick internally to trigger
                        // events like expanding SubComponents and pivots.
                        // By default a custom 'onClick' handler will override this functionality.
                        // If you want to fire the original onClick handler, call the
                        // 'handleOriginal' function.
                        if (handleOriginal) {
                          handleOriginal();
                        }
                      }
                    };
                  }}
                getTdProps={(state, rowInfo, column) => {

                    if (column.type === 'custom' && typeof column.Cell === 'function') {

                        return column.Cell;

                    } else {
                        return column.Cell = (row) => {

                            let cellValue = null;

                            if (column.type === 'storeAction' && !!column.action) {

                                cellValue = columnTypes[column.type](row, column, this.props[column.action]);

                            } else if (column.hasOwnProperty("type") && !!column.type) {

                                cellValue = columnTypes[column.type](row, column);

                            } else {

                                cellValue = (!row.value ? 'N/A' : row.value);
                            }

                            return cellValue;
                        }
                    }
                }}
            />
        );
    }
}

RequestList.propTypes = {
    data: PropTypes.array.isRequired,
    columns: PropTypes.array.isRequired
};

export default connect(null, mapDispatchToProps)(RequestList);