import Moment from "react-moment";
import StoreButton from "MCT/components/core/store-button";
import DialogLauncher from "MCT/components/core/dialog-launcher";
import ViewRequestDialog from "MCT/components/dialogs/view-request-dialog";
import React from "react";

export const requestColumns = [
    {
        Header: 'ID',
        accessor: 'id',
        type: 'custom',
        isAdmin: false,
        Cell: (row) => {
        return (
        <DialogLauncher className={"btn-mini"}
                                dialog={ViewRequestDialog}
                                dialogData={row.original}
                                buttonText={row.original.id}
                                color="secondary"
                                size="small"/>
        )

        }
    },
    {
        Header: "Status",
        accessor: "status",
        Cell: (row) => {

            const status = row.original.status;
            let color = "";

            if (status === "Submitted") {
                color = "#85cc00"
            }

            return (
                <p style={{color: color}} className={'m-0 p-0'}>{status}</p>
            )
        }
    },
    {
        Header: "Date Submitted",
        accessor: "dateSubmitted",
        Cell: (row) => {
            return (
                <Moment format="dd/mm/yyyy">{row.original.dateSubmitted}</Moment>
            )
        }
    },
    {
        Header: 'Description',
        accessor: 'description'
    },
    {
        Header: '',
        type: 'custom',
        className: 'action',
        isAdmin: true,
        headerClassName: 'action',
        Cell: (row) => {
            return (
                <DialogLauncher className={"btn-mini outlined-primary"}
                                dialog={ViewRequestDialog}
                                dialogData={row.original}
                                buttonText='View'
                                variant="outlined"
                                color="primary"
                                size="small"/>
            );
        }
    },
    {
        Header: '',
        type: 'storeAction',
        action: 'deleteRequest',
        text: 'Cancel',
        className: 'action',
        isAdmin: false,
        headerClassName: 'action'
    },
];